.image {
    border-radius: 10px;
    width: 400px;
    height: 400px;
    transition:         transform .8s ease-in-out;
  } 

.image:hover {
    transform: rotate(20deg);
  }

/* .header {
    flex: 50%;
} */
a:link {
    color: #3C64B1;
    text-decoration: none;
}
a:hover {
    color: #3C64B1;
    text-decoration: underline;
  }

.title {
    margin-left: -40px;
    font-family: 'Mulish', sans-serif;
    font-weight: 700;
    font-size: 72pt;
    color: #092E50;
    padding: 0px;
    line-height: 72pt;
}

 .About {
    display: flex;
} 

.contact {
    font-family: 'Inconsolata', monospace;
    font-weight: 500;
    text-transform: uppercase;
    font-size: 16px;
    color: #3C64B1;
    font-stretch: expanded;
    position: relative;
    margin-top: 85px;
    line-height: 12px;
}

 * {
    box-sizing: border-box;
  }
  
  .container {
    padding: 20px;
  }
  
  .row:after {
    content: "";
    display: table;
    clear: both
  }
  

  .bio {
    margin-top: 50px;
    margin-left: 35px;
    text-align: left;
    float: left;
    width: 55%;
    padding: 20px;
  }
  
  .portrait {
    float: left;
    width: 45%;
    padding: 20px;
  }
  
  .tag {
    /* font-family: 'Mulish', sans-serif;
    font-weight: 800; */
    font-family: 'Inconsolata', monospace;
    font-weight: 500;
    text-transform: uppercase;
    font-size: 16px;
    color: #3C64B1;
    font-stretch: expanded;
  }

  @media screen and (max-width: 1000px) {
    .portrait,
    .bio {
      width: 100%;
      text-align: center;
    }
  } 

  .descrip {
    color: #092E50;
    margin-top: 20px;
    height: 35px;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 11pt;
  }
  .clicker {
    background-color: #C4E7D6;
    font-family: 'Inconsolata', monospace;
    font-weight: 500;
    text-transform: uppercase;
    font-size: 16px;
    color: #3C64B1;
    font-stretch: expanded;
  }

  .emoji {
      font-size: 20px;
  }

